<template>
  <div>
    <el-alert
      v-show="uploaded"
      type="success"
      :title="successTip.title"
      :description="successTip.desc">
    </el-alert>
    <el-alert
      v-show="!uploaded"
      type="info"
      :title="importTip.title"
      :description="importTip.desc">
    </el-alert>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        successTip: {
          title: '',
          desc: '1. 2.3.',
        },
        importTip: {
          title: '',
          desc: '1. 2.',
        },
      };
    },
    computed: {
      uploaded() {
        return this.$store.state.uploaded;
      },
    },
  };
</script>

<style lang="css">
  .el-alert {
    position: absolute;
    z-index: 1;
  }
</style>



